.app{
  width: 100%;
}

header {
  color: black;
  padding: 33px 0 25px 15px;
  width: 100%;
  text-align: center;
}

header h1 {
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 0.3px;
  display: inline-block;
  margin: 0;
  vertical-align: middle;
}

.container-auth {
  display: -ms-flexbox;
  display: flex;
  max-width: 1100px;
  margin: 0 auto;
  padding-top: 30px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100vw;
}

.wrapper-auth {
  max-width: 1100px;
  margin: 0 auto;
}

.logForm {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  border-radius: 2px;
  /*border-top: 3px solid #103D5D;*/
  background-color: rgb(237, 237, 237);
  padding: 30px 18px;
  -webkit-box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
          box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
  width: 25%;
}

input {
  display: -ms-flexbox;
  display: flex;
  font-size: 22px;
  color: #000;
  padding: 18px 22px;
  font-size: 16px;
  margin-bottom: 17px;
  border: 0;
}

h2 {
  padding: 0;
  margin: 0;
  font-weight: 400;
}

form button {
  width: 100%;
  margin-top: 10px;
}

button {
  border-radius: 2px;
  min-width: 88px;
  background-color: #1a70ad /*#FA6900*/;
  cursor: pointer;
  border: 0;
  min-width: 120px;
  color: #fff;
  font-size: 14px;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
          box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
  padding: 10px 6px;
}

.errorBox{
  display: none;
}

.errorBox-active{
  display: -ms-flexbox;
  display: flex;
  padding: 20px;
  color:red;
}



